/* Home.css */

/* Main Content */
.container {
    text-align: center;
    margin-top: 50px;
  }
  
  h4 {
    color: #333;
  }
  
  .subtitle {
    color: #555;
  }
  
  /* About Me Button */
  .about-button {
    margin-top: 20px;
  }
  
  /* Social Media Icons */
  .social-icons {
    margin-top: 20px;
  
    .MuiSvgIcon-root {
      font-size: 2rem;
      margin: 0 10px;
      color: #333;
    }
  }
  
  /* Profile Picture */
  .profile-picture {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    overflow: hidden;
    margin: 20px auto;
    background-color: lightgray;
  
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  
  /* Footer */
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    background-color: #333;
    color: #fff;
    text-align: center;
  }
  